}

@mixin mediaSmMd {
  @media screen and (max-width: 768px) {
    @content;
  }
}

@mixin mediaSm {
  @media screen and (max-width: 320px) {
    @content;
  }
}

@mixin mediaMd {
  @media screen and (min-width: 321px) {
    @content;
  }
}

@mixin mediaLg {
  @media screen and (min-width: 769px) {
    @content;
  }
}

@mixin mediaXL {
  @media screen and (min-width: 1171px) {
    @content;
  }
}
